.DiaryEditor {
  border: 2px solid pink;
  border-radius: 10px;
  text-align: center;
  margin: 20px;
  background-color: rgb(253, 253, 248);
}

.diary__author,
.diary__content {
  width: 300px;
  padding: 10px;
}

.diary__content {
  height: 70px;
}

.diary_point {
  width: 160px;
  padding: 5px;
}

.diary__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.diary__author,
.diary__content,
.diary__emotion,
.diary.submit-btn {
  margin: 10px 0;
}

.diary__list {
  width: 300px;
  height: 80px;
  border: 1px solid black;
  margin: 10px;
}

.diary.submit-btn {
  padding: 12px;
  background-color: pink;
  color: white;
  border: none;
  border-radius: 5px;
}

.DiaryList {
  margin: 20px;
  padding: 20px;
  border: 2px solid pink;
  border-radius: 10px;
  background-color: rgb(253, 253, 248);
}

.DiaryList h2 {
  text-align: center;
}

.DiaryList .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.DiaryItem {
  width: 350px;
  background-color: rgb(254, 224, 229);
  border-radius: 15px;
  padding: 10px;
  margin: 10px 0;
}

.DiaryItem .info {
  display: flex;
  justify-content: space-between;
  padding: 0 10px 10px 10px;
  margin-bottom: 10px;
  border-bottom: 1.5px solid black;
}

.DiaryItem .date {
  font-size: 13px;
  color: rgb(143, 143, 143);
}

.DiaryList .content {
  padding: 0 10px;
}
